import { useEffect } from "react";
import { useParams } from "react-router-dom";
import { useState } from "react";
import Container from "@mui/material/Container";
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import Button from "@mui/material/Button";
import { useNavigate } from "react-router-dom";

const InputForm = (props) => {
  const navigate = useNavigate();

  function sendUpdate(formData) {
    console.log("on submit");
    const processId = formData.get("processId");
    const instanceCount = formData.get("instanceCount");
    const instanceDelay = formData.get("instanceDelay");
    const processVariables = formData.get("processVariables");

    var jsonData = {
      processId : "Process_140hg04",
      instanceCount : 1,
      instanceDelay : 1
    };

    console.log("form data: ", jsonData);

    const headers = new Headers();
    headers.append("Content-Type", "application/json")

    fetch(
      "http://localhost:8008/createFlow",
      {
        method: "POST",
        headers: new Headers({
          "Content-Type": "Application/JSON"
        }),
        //mode: "no-cors",
        body: JSON.stringify(jsonData)
      },
    );
    navigate("/form", { replace: true });
  }

  const handleSubmit = (event) => {
    console("handling submit")
    event.preventDefault();
    const simulationData = {
      processId : "Process_140hg04",
      instanceCount : 1,
      instanceDelay : 1
    };
    fetch("http://localhost:8008/createFlow", {
      method: "POST",
      headers: {
        "Content-Type": "Application/JSON",
      },
      body: JSON.stringify(simulationData)
    })
    .then((response) => response.json())
    .catch((error) => {
      console.log("error: ", error);
    })
  }

  return (
    <form action={sendUpdate}>
      <Container sx={{ width: 650, alignItems: "center" }}>
        <Paper elevation={1} sx={{ marginTop: 5 }}>
          <Box
            sx={{
              "& > :not(style)": { m: 2, width: "25ch" },
            }}
            noValidate
            autoComplete="off"
          >
            <TextField
              name="processId"
              id="processId"
              label="Process Id"
              variant="outlined"
            />
            <TextField
              name="instanceCount"
              id="instanceCount"
              label="instanceCount"
              variant="outlined"
            />
            <TextField
              name="instanceDelay"
              id="instanceDelay"
              label="instanceDelay"
              variant="outlined"
            />
            <TextField
              name="processVariables"
              id="processVariables"
              label="Process Variables"
              variant="outlined"
            />
            <Button type="submit" variant="contained">
              submit
            </Button>
          </Box>
        </Paper>
      </Container>
    </form>
  );
};
export default InputForm;
